___CSS_LOADER_EXPORT___.push([module.id, `*{
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}

body{
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
    background-size: cover;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

body > h1{
    color: white;
    font-size: 3rem;
    font-style: italic;
    margin-bottom: 0px;
}

body > h3 {
    color: white;
}

#big-container{
    background-color: rgba(145, 151, 184, 0.849);
    width: 80%;
    padding: 1.5rem;
    border-radius: 1rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

input{
    outline-width: 0;
    padding: 0.5rem;
    border-style: none;
}

button{
    padding: 0.5rem;
    background-color: rgb(85, 105, 221);
    border-style: none;
    color: white;
    border-radius: 0.25rem;
}

button:hover{
    background-color: rgb(48, 74, 223);
    cursor: pointer;
}

#loader-div{
    display: flex;
    align-items: center;
    gap: 1rem;
    padding: 2rem;
}

.loader {
  display: none;
  margin-top: 1rem;
  border: 0.5rem solid #f3f3f3; 
  border-top: 0.5rem solid #3498db;
  border-radius: 50%;
  width: 0.5rem;
  height: 0.5rem;
  animation: spin 1.5s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
